.basket {
  margin: 10px auto;
  padding: 20px;
  width: 80%;
  border: 1px #007bff;
}

.basket__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.basket__list--item {
  flex-direction: row;
  width: 20%;
  justify-content: space-between;
  padding: 10px;
}

.basket__list--item a {
  text-decoration: none;
  color: black;
}

.basket__list--item a:hover {
  text-decoration: underline #007bff;
  color: #007bff;
}

.basket__count {
  width: 50px;
  text-align: center;
  outline: none;
  color: #007bff;
  border: 0 solid;
}

.basket__empty {
  height: 100px;
  width: 100%;
  text-align: center;
  background-color: #007bff;
  font-size: 50px;
  line-height: 100px;
  color: #fff;
}
